export default {
  data () {
    return {
      gamesCount: 28,
      increment: 28
    }
  },
  computed: {
    displayedGames: function () {
      return this.games.slice(0, this.gamesCount)
    },
    showMoreBtnVisible: function () {
      return this.gamesCount < this.games.length
    }
  },
  methods: {
    showMore: function () {
      this.gamesCount += this.increment
    }
  }
}
