<template>
  <div class="loading-latout position-fixed top-0 start-0" v-if="isLoading">
    <semipolar-spinner
      :animation-duration="2000"
      :size="80"
      color="#b41f23"
      class="loading"
    />
  </div>
  <div class="games-filter-title">New games<span v-if="provider[0]">({{ provider[0] }})</span></div>
  <div class="games-catalog">
    <div class="catalog">
      <div class="catalog-subtitle">
        Total games: {{ games.length }}
      </div>
      <div class="catalog-list">
        <div class="catalog-item" v-for="(item, key) in displayedGames" :key="key">
          <div class="game">
            <div class="game-top">
              <div class="game-block-img">
                <img :src="item.imgUrl" :alt="item.name" class="game-img">
              </div>
              <div class="game-action">
                <div class="game-btn">
                  <div class="game-play">
                    <button class="btn">Play</button>
                  </div>
                </div>
              </div>
            </div>
            <div class="game-bottom">
              <div class="game-title">
                <a href="" class="link-no-styles">{{ item.name }}</a>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="catalog-more">
        <button class="btn btn-primary"  v-if="showMoreBtnVisible" @click="showMore">Show more</button>
      </div>
    </div>
  </div>
</template>

<script>
import loadingMixin from '@/mixins/loadingMixin'
import showGames from '../mixins/showGames'
import filterProvider from '@/mixins/filterProvider'
export default {
  data () {
    return {
      games: [],
      providersFilter: [],
      catagory: 'new'
    }
  },
  mixins: [showGames, loadingMixin, filterProvider],
  methods: {
    filter () {
      this.games = []
      this.tempGames.forEach((item) => {
        if (item.isNew) {
          this.games.push(item)
        }
      })
    },
    providerFilter () {
      this.providersFilter = [...new Set(this.games.map(item => item.gameProvider))]
    },
    sendData () {
      this.emitter.emit('sendProvidersFilter', this.providersFilter)
      this.emitter.emit('sendCatagory', this.catagory)
    }
  },
  created () {
    this.filter()
    this.providerFilter()
    this.sendData()
  },
  inject: ['tempGames']
}
</script>
